@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.Card {
    /*background-color: rgb(47, 173, 177);*/
    background-color: #00b48c;
    color: #071e4f;
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    width: 50%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    box-shadow: 5px 5px black;
    transform: rotate(-5deg);
}

.Card:hover {
    border: 3px solid black;
}



.title {
    padding: 1%;
    text-align: center;
}

.translation {
    text-align: center;
}

.definition {
    padding: 0.5%;
    font-size: larger;
    font-weight: bold;
    text-align: center;
}

.code-box {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}


.ar-code, .qr-code {
    width: 15%;
    height: 15%;
    padding: 2%;
    transition: transform .4s;
}

.ar-code:hover, .qr-code:hover{
    transform: scale(4);

}

.ar-info, .qr-info {
    font-family: 'Roboto', sans-serif;
    font-weight: bolder;
    font-style: italic;
}

.title-box {
    background-color: #071e4f;
    font-size: x-small;
    color: white;
    width: 40%;
    height: 10%;
    border-radius: 500% 0% 200% 0% / 0% 500% 500% 200%;
    border: 2px solid #00b48c;
    padding: 0.5%;
}