@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.btn-box {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

button {
    margin-top: 2%;
    font-size: larger;
    font-family: 'Roboto', sans-serif;
    padding: 1% 3% 1% 3%;
    border: 2px solid black;
}


.ion-md-checkmark {
    font-size: xx-large;
    color: whitesmoke;
}

.ion-md-fastforward {
    font-size: xx-large;
    color: whitesmoke;
}

#found {
    background-color: rgb(20, 166, 61);
    border-radius: 20px;
}

#found:hover {
    background-color: rgb(12, 135, 47);
}

#skip {
    background-color: rgb(164, 117, 13);
    border-radius: 20px;
}

#skip:hover {
    background-color: rgb(134, 96, 13);
}

#score {
    text-align: center;
    font-size: xx-large;
    font-family: 'Roboto', sans-serif;
    color: #00b48c;
}